<template>
  <div class="address">
    <van-nav-bar title="收货地址" left-arrow @click-left="$router.go(-1)" />

    <div class="boxAdd">
      <div class="box" v-for="item in list" :key="item.address_id">
        <div class="hder">
          <p>
            <span>{{ item.name }}</span
            >&nbsp; <span>{{ item.phone }}</span>
          </p>
          <p>&nbsp;</p>
          <p>
            {{
              item.region.province +
              item.region.city +
              item.region.region +
              item.detail
            }}
          </p>
        </div>
        <div class="bot">
          <div>
            <van-radio-group v-model="radio">
              <van-radio
                @click="chenage(item.address_id)"
                :name="item.address_id"
              >
                {{ radio === item.address_id ? '默认' : '选择' }}</van-radio
              >
            </van-radio-group>
          </div>
          <div>
            <span @click="onEdit"><van-icon name="edit" />编辑</span>&nbsp;

            <span @click="remove(item.address_id, item.detail)"
              ><van-icon name="delete-o" />删除</span
            >
          </div>
        </div>
      </div>
    </div>
    <div class="but" @click="onAdd"><div>添加地址</div></div>
  </div>
</template>

<script>
import { getAddressList, removeAddress, addRess } from '@/api/address'
import { Dialog } from 'vant'
export default {
  name: 'addreddIndex',
  data() {
    return {
      list: [],
      radio: null,
      addressList: []
    }
  },

  computed: {
    radioId() {
      return this.list[0].address_id
    },
    detail() {
      return this.list.detail
    }
  },
  async created() {
    const {
      data: { list }
    } = await getAddressList()

    this.list = list
    this.radio = JSON.parse(localStorage.getItem('address_id'))
    this.addressList = JSON.parse(localStorage.getItem('address' + this.detail))
  },
  methods: {
    onAdd() {
      this.$router.push({
        path: '/address/updata',
        query: { text: '新增地址' }
      })
    },
    onEdit() {
      this.$router.push({
        path: '/address/updata',
        query: { text: '修改地址' }
      })
    },
    async remove(id, dizhi) {
      Dialog.confirm({
        title: '提示',
        message: '您确定要删除当前收货地址吗'
      })
        .then(async () => {
          await removeAddress(id)
          localStorage.removeItem('address' + dizhi)
          // on confirm
        })
        .catch(() => {
          // on cancel
        })
    },
    async chenage(id) {
      await addRess(id)

      localStorage.setItem('address_id', JSON.stringify(id))
    }
  }
}
</script>

<style lang="less" scoped>
.address {
  position: relative;
  // background-color: #ddd;
  .but {
    background-color: #fff;
    position: Fixed;
    width: 100%;
    bottom: 0;
    height: 70px;

    div {
      margin: 17px auto;
      text-align: center;
      color: #fff;
      width: 88%;
      height: 40px;
      line-height: 40px;
      background-color: red;
      border-radius: 20px;
    }
  }
}
.box {
  background-color: #ffe6e6;
  border: 1px solid #ddd;
  padding: 20px;
  margin: 10px;
  border-radius: 10px;
  .hder {
    padding-bottom: 20px;
    border-bottom: 1px solid #ddd;
  }
  .bot {
    padding-top: 20px;
    display: flex;
    justify-content: space-between;
  }
}
</style>
